import { request } from 'utils';

const prefix = '/api/categories';

export const getListCourse = (page = 1) =>
  request({
    prefix,
    url: '/Course',
    params: {
      page,
      IsPagingEnabled: true,
    },
    method: 'GET',
  });
export const getCourse = id =>
  request({
    prefix,
    url: `/Course/GetById`,
    params: { id },
    method: 'GET',
  });
export const deleteCourse = id =>
  request({
    prefix,
    url: `/Course`,
    data: { id },
    method: 'DELETE',
  });
export const updateCourse = (id, values) =>
  request({
    prefix,
    url: `/Course`,
    data: {
      id,
      label: values.code,
      fromYear: values.fromYear,
      toYear: values.toYear,
    },
    method: 'PUT',
  });

export const createCourse = values =>
  request({
    prefix,
    url: '/Course',
    data: {
      id: values.id,
      label: values.code,
      fromYear: values.fromYear,
      toYear: values.toYear,
    },
    method: 'POST',
  });



