import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import moment from 'moment';
import { Row, Card, Form, Input, Spin, Select, message } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { getCourse, updateCourse, createCourse } from 'services/ListCourse';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    code: '',
    labelInEnglish: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

// eslint-disable-next-line prefer-destructuring
const Option = Select.Option;

@Form.create({ name: 'formSpcializeTraining' })
class FormSpecializeTrainingPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      systemId: false,
      initialValue: {},
      loading: false,
      listFromDate: [],
      listToDate: [],
      disableCode: false,
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    const fromDate = [];
    const toDate = [];
    const currentYear = moment().years();
    // eslint-disable-next-line no-plusplus
    for (let i = 0; i < 5; i++) {
      fromDate.push(currentYear - i);
      toDate.push(currentYear + i);
    }
    this.setState({
      listFromDate: fromDate,
      listToDate: toDate,
    });

    if (match.params.id) {
      this.setState({ systemId: match.params.id, loading: true, disableCode: true });
      getCourse(match.params.id)
        .then(res => {
          const initialValue = handleInitialValue(res.data);
          this.setState({ initialValue, loading: false });
        })
        .catch(error => {
          console.log('error', error);
          this.setState({ loading: false });
        });
    }
  };

  handleSubmit = e => {
    const { match } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (match.params.id) {
          console.log('updateCourse==========>', values);
          updateCourse(match.params.id, values)
            .then(() => {
              message.success('Chỉnh sửa khóa đào tạo thành công');
              this.props.history.push('/courses/list');
            })
            .catch(error => {
              console.log('res', error);
              // message.error(error.response.data.message);
            });
        } else {
          console.log('createCourse==========>', values);
          createCourse(values)
            .then(() => {
              message.success('Thêm khóa đào tạo thành công');
              this.props.history.push('/courses/list');
            })
            .catch(error => {
              console.log('res', error.response);
              // message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId, listFromDate, listToDate, disableCode } = this.state;
    return (
      <Card title={systemId ? 'Cập nhật khóa đào tạo' : 'Thêm khóa đào tạo'}>
        <Spin spinning={this.state.loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item {...formItemLayout} label="Mã khóa" hasFeedback>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input disabled={disableCode} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên khóa" hasFeedback>
                {getFieldDecorator('code', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Từ năm" hasFeedback>
                {getFieldDecorator('fromYear', {
                  initialValue: initialValue.fromYear,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(
                  <Select showSearch style={{ width: '100%' }} placeholder="Chọn năm">
                    {listFromDate.map(item => (
                      <Option key={item}>{item}</Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Đến năm" hasFeedback>
                {getFieldDecorator('toYear', {
                  initialValue: initialValue.toYear,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(
                  <Select showSearch style={{ width: '100%' }} placeholder="Chọn năm">
                    {listToDate.map(item => (
                      <Option key={item}>{item}</Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSpecializeTrainingPage {...props} />
  </Page>
));



